.App {
  text-align: center;
  opacity: 0.8;
  background-color: #e7e7e7;
  position: fixed;
  width: 100%;
  height: 100%;
  top: 0px;
  left: 0px;
  z-index: 1000;
  font-weight: 500 !important;
  font-family: "Roboto", "Helvetica", "Arial", sans-serif !important;
}

.init-screen {
  width: 400px;
  margin: auto;
  margin-top: 10%;
}

.center-panel {
  display: table;
  position: absolute;
  top: 0;
  left: 0;
  height: 100%;
  width: 100%;
}

.center-panel-content {
  display: table-cell;
  vertical-align: middle;
}