.App .statistic-panel-body {
    position: absolute;
    top: 12%;
    right: 5%;
    width: 200px;
    height: 250px;
    border: 1px solid;
    border-radius: 11px;
}

.content .end-shoe-statistic-panel-body {
    text-align: center;
    box-shadow: unset;
}

.statistic-panel-title {
    padding-top: 16px;
}

.sub-header {
    padding-bottom: 5px;
}

.how-to-play-section {
    padding-top: 5px;
    padding-bottom: 5px;
    font-size: 21px;
}

.content .how-to-play-section{
    font-size: 21px;
}

.how-to-play-section a {
    display: inline;
    font-size: 21px;
}

.title h2 {
    font-size: 32px;
    text-align: center;
}

.content .sub-header  {
    font-size: 21px;
}

.content p {
    font-size: 18px;
}

.dialog .start-button {
    -webkit-justify-content: center;
            justify-content: center;
}

.dialog .start-button button {
    font-size: 20px;
}
.board-seperator {
    text-align: center;
}

.dealer-panel {
    display: -webkit-inline-flex;
    display: inline-flex;
    text-align: center;
    height: 170px;
    margin-top: 6px;
}

.board-wrapper {
    display: inline-block;
    margin-top: 40px;
}

.player-panel {
    -webkit-justify-content: center;
            justify-content: center;
    display: -webkit-flex;
    display: flex;
}
.App .app-header {
  background-color: #162db3;
}

.app-header {
  min-height: 60px;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: left;
          align-items: left;
  -webkit-justify-content: left;
          justify-content: left;
  font-size: calc(10px + 2vmin);
  color: white;
}

.app-header button {
  position: absolute;
  right: 30px;
  border: 1px solid;
  border-radius: 15px;
  z-index: 9999;
}

.app-header button svg {
  padding-left: 5px;
}

.app-header .header-font {
  font-size: 30px;
}
.control-panel .control-panel-body {
    height: 75px;
    margin: auto;
    width: 500px;
    margin-top: 4.0%;
    border: 1px solid;
    border-radius: 10px;
}

.control-panel .label-font {
    font-size: 16px;
    font-weight: 500;
    font-family: "Roboto", "Helvetica", "Arial", sans-serif;
    color: black;
}

.control-panel .button-play {
    font-size: 40px;
    color: green;
}

.control-panel .button-hit {
    font-size: 40px;
    color: blue;
}

.control-panel .button-stand {
    font-size: 40px;
    color: red;
}

.control-panel .button-reload {
    font-size: 40px;
    color: black;
}

.control-panel .button-disabled {
    cursor: pointer;
    pointer-events: none;
}

.control-panel .button-disabled .button-hit {
    color: grey;
}

.control-panel .button-disabled .button-stand {
    color: grey;
}

.control-panel .button-disabled .button-play {
    color: grey;
}

.control-panel .button-disabled .label-font {
    color: grey;
    font-weight: 500;
    font-family: "Roboto", "Helvetica", "Arial", sans-serif;
}




.summary-panel-body {
    margin: auto;
    width: 300px;
}

.summary-section {
    padding: 5px 5px 5px 30px;
    text-align: left;
}
.sub-header {
    padding-bottom: 5px;
}

.how-to-play-section {
    padding-top: 5px;
    padding-bottom: 5px;
    font-size: 21px;
}

.content .how-to-play-section{
    font-size: 21px;
}

.how-to-play-section a {
    display: inline;
    font-size: 21px;
}

.title h2 {
    font-size: 32px;
}

.content .sub-header  {
    font-size: 21px;
}

.content p {
    font-size: 18px;
}

.dialog .start-button {
    -webkit-justify-content: center;
            justify-content: center;
}

.dialog .start-button button {
    font-size: 20px;
}
.App {
  text-align: center;
  opacity: 0.8;
  background-color: #e7e7e7;
  position: fixed;
  width: 100%;
  height: 100%;
  top: 0px;
  left: 0px;
  z-index: 1000;
  font-weight: 500 !important;
  font-family: "Roboto", "Helvetica", "Arial", sans-serif !important;
}

.init-screen {
  width: 400px;
  margin: auto;
  margin-top: 10%;
}

.center-panel {
  display: table;
  position: absolute;
  top: 0;
  left: 0;
  height: 100%;
  width: 100%;
}

.center-panel-content {
  display: table-cell;
  vertical-align: middle;
}
